import { useEffect } from "react";
import {
    useWindowWidth,
} from '@react-hook/window-size';
import ReactGA from 'react-ga';

function isScriptLoaded(url) {
  const scripts = document.getElementsByTagName('script');
  for (let i = scripts.length; i--;) {
    if (scripts[i].src === url) return true;
  }
  return false;
}

function isAdLoaded(id) {
  const ctr = document.getElementById(id);
  return Boolean(ctr && ctr.firstChild);
}

function loadTag(adsData, isMobileDevice ) {
  try {
    window._mNHandle.queue.push(function () {
      adsData.forEach(adData => {
        const { id, size, id2, isMobile: adIsForMobile } = adData;

        if (!isMobileDevice && !adIsForMobile && !isAdLoaded(id)) {
          window._mNDetails.loadTag(id, size, id2);
        } else if (!isAdLoaded(id) && isMobileDevice && adIsForMobile) {
          window._mNDetails.loadTag(id, size, id2);
        }
      });
    });
  }
  catch (error) {
    ReactGA.event({
      category: 'error',
      action: 'error',
      value: `${error}`,
      label: `${error}`,
    });
  }
}

export default function AdsInitializer({
  adsData
}) {
  const onlyWidth = useWindowWidth();

  useEffect(() => {
    const isMobileDevice = Boolean(onlyWidth <= 728);

    if (adsData && adsData.length > 0) {
      const scriptUrl = "https://contextual.media.net/dmedianet.js?cid=8CUI6S7IJ";
      const isLoaded = isScriptLoaded(scriptUrl);

      if (!isLoaded) {
        window._mNHandle = window._mNHandle || {};
        window._mNHandle.queue = window._mNHandle.queue || [];
        const script = document.createElement("script");
        script.src = scriptUrl;
        script.async = true;
        script.onload = () => {
          loadTag(adsData, isMobileDevice);
          if (isMobileDevice) {
            ReactGA.event({
              category: 'ShowAdsOnMobile',
              action: 'ShowAdsOnMobile',
            });
          }
          ReactGA.event({
            category: 'MediaAd',
            action: 'Appeared',
          });
        }
        document.body.appendChild(script);
      } else {
        loadTag(adsData, isMobileDevice);
      }
    }

    return () => {
    };
  });

  return null;
}
