import React from "react";
import {
    useWindowWidth,
} from '@react-hook/window-size';
import LazyLoad from 'react-lazyload';
import AdsInitializer from "../AdsInitializer";

export default function Template({
  type
}) {
  const onlyWidth = useWindowWidth();
  const isMobile = onlyWidth <= 728;

  if (isMobile && type === "blog-content") {
    const adsData = [{
      id: "188970836", size: "300x250", id2: "188970836", isMobile: true 
    }];

    return (
      <LazyLoad height={250} offset={650}>
        <>
          <AdsInitializer adsData={adsData} />
          <div className="AdsContainer" id={"188970836"}></div>
        </>
      </LazyLoad>
    );
  } else if (isMobile && type === "billboard") {
    const adsData = [{
      id: "617163306", size: "320x50", id2: "617163306", isMobile: true 
    }];

    return (
      <LazyLoad height={50} offset={650}>
        <>
          <AdsInitializer adsData={adsData} />
          <div className="AdsContainer" id={"617163306"}></div>
        </>
      </LazyLoad>
    );
  } else if (isMobile) {
    return null;
  }

  if (type === "leaderboard") {
    const adsData = [{
      id: "138821154", size: "728x90", id2: "138821154", isMobile: false
    }];

    return (
      <LazyLoad height={90} offset={650}>
        <>
          <AdsInitializer adsData={adsData} />
          <div className="AdsContainer" id={"138821154"}></div>
        </>
      </LazyLoad>
    );
  }

  if (type === "blog-content") {
    const adsData = [{
      id: "188970836", size: "300x250", id2: "188970836", isMobile: false
    }];

    return (
      <LazyLoad height={250} offset={650}>
        <>
          <AdsInitializer adsData={adsData} />
          <div className="AdsContainer" id={"188970836"}></div>
        </>
      </LazyLoad>
    );
  }

  if (type === "billboard") {
    const adsData = [{
      id: "426407765", size: "970x250", id2: "426407765", isMobile: false
    }];

    return (
      <LazyLoad height={250} offset={650}>
        <>
          <AdsInitializer adsData={adsData} />
          <div className="AdsContainer" id={"426407765"}></div>
        </>
      </LazyLoad>
    );
  }

  return null;
}
